@use 'global' as *;
@use 'sass:math';

.p-search {
    position: relative;
    background-color: $white;
    padding-bottom: rem(24);
    border-top: 2px solid #171717;
    border-bottom: 2px solid #171717;
    @include mq(md) {
        padding: rem(60) 0;
    }
}

.p-search__inner.l-inner {


}

.p-search__title {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: rem(-27);
    border: 2px solid #171717;
    border-radius: rem(10);
    max-width: rem(240);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $white;
    padding: rem(12) 0;
    @include mq(md) {
        max-width: rem(295);
    }
}

.p-search__title img {
    width: rem(23);

}

.p-search__title span {
    font-weight: 400;
    display: inline-block;
    font-size: rem(28);
    margin-left: rem(15);
}

.p-search__form {


}

.p-search__form-row {
    padding: rem(40) 0 rem(12);
}

.p-search__form-row + .p-search__form-row {
    border-top: 1px dashed #171717;
}

.p-search__subtitle {
    @include mq(md) {
        font-size: rem(20);
    }
}

.p-search__form-row dd {
    margin-top: rem(20);
    margin-left: rem(-22);
    @include mq(md) {
        margin-left: rem(-28);
        margin-top: 0;
    }
}

.p-search__form-row--center dd {
    text-align: center;
    @include mq(md) {
        text-align: left;
    }
}

.p-search__form-row label {
    display: inline-block;
    margin-top: rem(24);
    margin-left: rem(22);
}

.p-search__form-row .c-tag {
    border-width: rem(1);
    border-radius: rem(6);
    padding: rem(5) rem(10);
}

.p-search__form-row--text {
    @include mq(md) {
        display: flex;
        align-items: center;
    }
}

.p-search__form-row--text dd {
    margin-left: 0;
    @include mq(md) {
        margin-left: rem(28);
    }
}

.p-search__form-row input {

}

.p-search__form-row input[type="radio"] {

}

.p-search__form-row input[type="text"] {
    max-width: rem(262);
    width: 100%;
    border: 1px solid #171717;
    border-radius: rem(32);
    padding: rem(12);
    background: url(../images/common/search.svg) 5% center / 23px no-repeat;
    padding-left: rem(40);
}

.p-search__form-row input[type="text"]::placeholder {
    text-align: center;
}