@use 'global' as *;
@use 'sass:math';

.p-pageMv {
    background-color: $green;
    padding-top: rem(37);
}

.p-pageMv__inner {

}

.p-pageMv__box {
    background-color: $white;
    border: 1px solid #171717;
    border-radius: rem(20);
    @include mq(md) {
        border-width: rem(2);
    }
}

.p-pageMv__box-head {
    font-size: rem(20);
    text-align: center;
    line-height: math.div(rem(29),rem(20));
    padding: rem(27) 0;
    border-bottom: 1px solid #171717;
    @include mq(md) {
        font-size: rem(32);
        line-height: math.div(rem(46),rem(32));
        padding: rem(21) 0;
    }
}

.p-pageMv__box-main {
    padding: rem(34) rem(13) rem(111);
    @include mq(md) {
        max-width: rem(624);
        margin: 0 auto;
        padding: rem(43) 0;
    }
}

.p-pageMv__box-text {
    font-size: rem(16);
    font-weight: 400;
    line-height: math.div(rem(23),rem(16));

}

.p-pageMv__caution {
    margin-top: rem(20);
    font-size: rem(16);
    font-weight: 400;
    line-height: math.div(rem(23),rem(16));
}