@use 'global' as *;
@use 'sass:math';

.p-globalNav {
    width: rem(60);
    height: rem(60);
    @include mq(md) {
        position: relative;
        z-index: 5;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 100%;
        height: rem(90);
    }
}

.p-globalNav__menus {
    display: none;
    @include mq(md) {
        position: relative;
        max-width: rem(440);
        width: 100%;
        height: rem(52);
        background-color: $white;
        display: grid;
        grid-template-columns: repeat(4,1fr);
        border: 2px solid #171717;
        border-radius: rem(10);
    }
}

.p-globalNav__menu {
    transition: all 0.3s;
}

.p-globalNav__menu:first-child {
    border-radius: rem(10) 0 0 rem(10);
}

.p-globalNav__menu:last-child {
    border-radius: 0 rem(10) rem(10) 0;
}

.p-globalNav__menu.is-selected {
    background-color: #F1EFDE;
}

.p-globalNav__menu a {
    @include mq(md) {
        width: 100%;
        height: 100%;
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

.p-globalNav__menu img {
    @include mq(md) {
        width: rem(19);
        margin: 0 auto;
    }
}

.p-globalNav__menu span {
    display: inline-block;
    font-size: rem(16);
    margin-top: rem(2);
}

.p-globalNav__contact {
    position: relative;
    border: 2px solid #F8DC27;
    border-radius: rem(20);
    background-color: $white;
    width: 100%;
    height: 100%;
    @include mq(md) {
        border: 2px solid $black;
        border-radius: 50%;
        width: rem(90);
        height: rem(90);
        margin-left: rem(127);
        transition: all 0.3s;
    }
}

.p-globalNav__contact:hover {
    @include mq(md) {
        background-color: $black;
        color: $white;
    }
}

.p-globalNav__contact::before {
    @include mq(md) {
        position: absolute;
        content: "";
        width: rem(94);
        height: rem(94);
        top: rem(-4);
        left: rem(-4);
        border: 2px solid #F8DC27;
        border-radius: 50%;
        z-index: -1;
    }
}

.p-globalNav__contact:hover .p-globalNav__contact-img {
    @include mq(md) {
        background-color: $white;
    }
}

.p-globalNav__contact-link {
    width: 100%;
    height: 100%;
}

.p-globalNav__contact-link a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    @include mq(md) {
        flex-direction: column;
        padding-top: rem(5);
    }
}

.p-globalNav__contact-link span {
    font-size: min(0.83vw,rem(12));
}

.p-globalNav__contact-img {
    mask: url(../images/common/mail.svg) center center / contain no-repeat;
    width: rem(32);
    height: rem(32);
    background-color: $yellow;
    @include mq(md) {
        background-color: $black;
        transition: all 0.3s;
    }
}

.p-globalNav__nav {
    @include mq(md) {
        display: none;
        position: absolute;
        max-width: rem(440);
        width: 100%;
        background-color: #F1EFDE;
        left: rem(-2);
        top: rem(44);
        border-radius: 0 0 rem(10) rem(10);
        padding: rem(22) rem(66);
        z-index: -1;
    }
}

.p-globalNav__subMenus {
    @include mq(md) {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: rem(20) rem(60) ;
    }
}

.p-globalNav__subMenu {
    position: relative;
}

.p-globalNav__subMenu a {
    @include mq(md) {
        font-size: rem(16);
        align-items: flex-start;
        line-height: math.div(rem(23),rem(16));
    }
}

.p-globalNav__subMenu span {
    @include mq(md) {
        position: relative;
        display: inline-block;
        letter-spacing: -0.05em;
    }
}

.p-globalNav__subMenu span::before {
    position: absolute;
    content: "";
    width: 0;
    height: rem(2);
    background-color: $yellow;
    left: 0;
    bottom: 0;
    transition: all 0.3s;
}

.p-globalNav__subMenu.is-selected span::before {
    width: 100%;
}
