@use 'global' as *;
@use 'sass:math';

.p-form {

}

.p-form__row {
    border-bottom: 1px dashed #171717;
    padding: rem(40) 0 rem(61);
    @include mq(md) {
        padding: rem(20) 0 rem(40);
    }
}

.p-form__row--message{
    border-bottom: none;
}

.p-form__row dt {
    max-width: rem(320);
    width: 100%;
    margin: 0 auto;
    font-size: rem(20);
    @include mq(md) {
        max-width: rem(673);
    }
}

.p-form__row dd {
    max-width: rem(320);
    width: 100%;
    margin: 0 auto;
    margin-top: rem(15);
    @include mq(md) {
        max-width: rem(673);
    }
}

.p-form__row input {
    width: 100%;
    font-size: rem(20);
    padding: rem(15) rem(10);
    border: 1px solid #171717;
    border-radius: rem(30);
    background-color: #F5F5F5;
}

.p-form__row textarea {
    resize: none;
    height: rem(380);
    width: 100%;
    font-size: rem(20);
    padding: rem(15) rem(10);
    border: 1px solid #171717;
    border-radius: rem(30);
    background-color: #F5F5F5;
}

.p-form__outside {
    margin-bottom: rem(-312);
    margin-top: rem(40);
    @include mq(md) {
       margin-bottom: rem(-280);
       margin-top: rem(80);
    }
}

.p-form__text {
    font-size: rem(16);
    line-height: math.div(rem(23),rem(16));
    @include mq(md) {
        max-width: rem(704);
        margin: 0 auto;
    }
}

.p-form__text a {
    color: $white;
}

.p-form__btn {
    max-width: rem(345);
    margin: 0 auto;
    margin-top: rem(60);
    @include mq(md) {
        max-width: rem(260);
    }
}

.p-form__btn .c-btn {
    font-size: rem(20);
}