@use 'global' as *;
@use 'sass:math';

.p-animals {
    position: relative;
}

.p-animals__inner {

}

.p-animals__title {
    text-align: center;
}

.p-animals__cards {
    margin-top: rem(32);
}

.p-animals__wrap {
    @include mq(md) {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
    }
}

.p-animals__card {
    position: relative;
    width: 100%;
    border: 2px solid #00262B;
    background-color: $white;
    border-radius: rem(10);
    padding: rem(24) rem(20) rem(32);
    box-shadow: rem(5) rem(5) 0 $white;
    @include mq(md) {
        max-width: rem(480);
        padding: rem(24) rem(20) rem(66);
        border-radius: rem(20);
        box-shadow: rem(10) rem(10) 0 $white;
    }
}



.p-animals__card--top {
    position: relative;
    padding-bottom: rem(14);
    border-bottom: 2px solid #000;
}

.p-animals__card-img {
    width: 100%;
    position: relative;
    border-radius: rem(10);
    overflow: hidden;
}

.p-animals__card-img::before {
    display: block;
    content: "";
    padding-top: 100%;
    @include mq(md) {
        padding-top: 98.85%;
    }
}

.p-animals__card-img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: rem(10);
}

.p-animals__card--date {
    position: absolute;
    bottom: rem(-3);
    left: 0;
    border-radius: rem(10) rem(10) 0 0;
}

.p-animals__card--bottom {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: rem(-22);
    @include mq(md) {
        padding-top: rem(23);
        margin-left: rem(-26);
    }
}

.p-animals__card-tag {
    margin-top: rem(20);
    margin-left: rem(22);
    @include mq(md) {
        margin-top: rem(26);
        margin-left: rem(26);
    }
}

.p-animals__card--sub {
    padding: rem(48) rem(12);
    margin-top: rem(20);

    @include mq(md) {
        max-width: unset;
        width: calc(100% - #{rem(480)});
        margin-left: rem(80);
        padding: rem(59) rem(20);
    }
}

.p-animals__card--sub02 {
    @include mq(md) {
        margin-left: 0;
        margin-right: rem(80);
    }
}

.p-animals__card-title {
    max-width: rem(300);
    font-family: $second-font-family;
    font-size: rem(24);
    font-weight: 400;
    margin: 0 auto;
    @include mq(md) {
        max-width: unset;
    }
}

.p-animals__card-area {
    max-width: rem(300);
    font-family: $second-font-family;
    font-size: rem(24);
    font-weight: 400;
    margin: 0 auto;
    margin-top: rem(30);
    @include mq(md) {
        max-width: unset;
        margin-top: rem(40);
    }
}

.p-animals__card-text {
    max-width: rem(300);
    width: 100%;
    margin: 0 auto;
    margin-top: rem(30);
    @include mq(md) {
        max-width: unset;
        margin-top: rem(40);
    }
}

.p-animals__card-btn {
    max-width: rem(320);
    margin: 0 auto;
    margin-top: rem(60);
    @include mq(md) {
        max-width: rem(280);
        margin-top: rem(56);
    }
}

.p-animals__border {
    width: 100vw;
    border-color: #171717;
    margin-top: rem(60);
    margin-left: calc(50% - 50vw);
    @include mq(md) {
        display: none;
    }
}

.p-animals__wrap--reverse {
    margin-top: rem(60);
    @include mq(md) {
        flex-direction: row-reverse;
        margin-top: rem(40);
    }
}

.p-animals__tree01 {
    position: absolute;
    width: rem(219);
    right: calc(50% - #{rem(639)});
    top: rem(50);
}

.p-animals__tree02 {
    position: absolute;
    width: rem(243);
    top: rem(724);
    left: calc(50% - #{rem(192)});
}

.p-animals__tree03 {
    position: absolute;
    width: rem(210);
    bottom: rem(-80);
    left: calc(50% - #{rem(724)});
}