@use 'global' as *;
@use 'sass:math';

.p-singleMv {
    background-color: $green;
    padding-top: rem(43);
    @include mq(md) {
        max-width: rem(1440);
        margin: 0 auto;
        padding-top: rem(80);
        border-radius: rem(36) 0 0 0;
    }
}

.p-singleMv__inner.l-inner {
    padding: 0;
    @include mq(md) {
        padding: 0 rem(15);
    }
}

.p-singleMv__items{
    @include mq(md) {
        background-color: $white;
        border: 2px solid #171717;
        border-radius: rem(20);
        padding: rem(60);
    }
}

.p-singleMv__item {
    position: relative;
    width: 100%;
    border-radius: rem(10);
    overflow: hidden;
}

.p-singleMv__item::before {
    display: block;
    content: "";
    padding-top: 96.15%;
    @include mq(md) {
        padding-top: 28%;
    }
}

.p-singleMv__item img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

.p-singleMv {
    .next-arrow {
        right: rem(10);
        @include mq(md) {
            right: rem(70);
        }
    }
    .prev-arrow {
        left: rem(10);
        @include mq(md) {
            left: rem(70);
        }
    }
}