@use 'global' as *;
@use 'sass:math';

.p-contact {
    background-color: $green;
    padding-bottom: rem(320);
    @include mq(md) {
        padding-bottom: rem(260);
    }
}

.p-contact__inner {

}

.p-contact__box {
    border: 1px solid #171717;
    border-radius: rem(20);
    background-color: $white;
    padding-bottom: rem(40);
}

.p-contact__head {
    padding: rem(42) 0 rem(32);
    overflow: hidden;
    border-bottom: 1px dashed #171717;
}

.p-contact__step {
    max-width: rem(230);
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    @include mq(md) {
        max-width: rem(666);
    }
}

.p-contact__step-item {

}

.p-contact__step-item + .p-contact__step-item {
    margin-left: rem(65);
    @include mq(md) {
        margin-left: rem(284);
    }
}

.p-contact__step-check {
    position: relative;
    width: rem(16);
    height: rem(16);
    border: 1px solid #171717;
    border-radius: 50%;
    margin: 0 auto;
    background-color: $white;
    @include mq(md) {
        width: rem(37);
        height: rem(37);
    }
}

.p-contact__step-check::before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    width: rem(82);
    height: rem(1);
    background-color: #171717;
    left: rem(-83);
    @include mq(md) {
        width: rem(284);
        left: rem(-285);
    }
}

.p-contact__step-text {
    font-size: rem(16);
    line-height: math.div(rem(23),rem(16));
}

.p-contact__step-item.is-checked {
    .p-contact__step-text {
        color: $yellow;
    }
    .p-contact__step-check {
        background-color: $yellow;
    }
    .p-contact__step-check::before {
        background-color: $yellow;
    }
}

.p-contact__step-item:last-child {
    .p-contact__step-check::after {
        position: absolute;
        content: "";
        top: 50%;
        transform: translateY(-50%);
        width: rem(83);
        height: rem(1);
        background-color: #171717;
        right: rem(-84);
        @include mq(md) {
            width: rem(284);
            right: rem(-285);
        }
    }
}

.p-contact__main {


}
