@use 'global' as *;

.p-cards {

}

.p-cards__inner.l-inner {
    padding-left: rem(25);
    padding-right: rem(25);
}

.p-cards__wrap {
    @include mq(md) {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: rem(72);
    }
}

.p-cards__card {

}

.p-cards__card + .p-cards__card {
    margin-top: rem(32);
    @include mq(md) {
        margin-top: 0;
    }
}

.p-cards__nav {
    margin-top: rem(36);
    @include mq(md) {
        margin-top: rem(80);
    }
}