@use 'global' as *;

.c-tag {
    display: inline-block;
    font-size: rem(16);
    border: 1px solid #00262B;
    border-radius: rem(5);
    padding: rem(6) rem(20);
    background-color: $white;
    @include mq(md) {
        border-width: rem(2);
    }
}

.c-tag--yellow {
    background-color: $yellow;
}

.c-tag--none {
    border: none;
}

.c-tag--cat {
    border-width: rem(1);
    font-family: $second-font-family;
    font-weight: 400;
    line-height: normal;
    padding: rem(2) rem(12);
}