@use 'global' as *;

.c-title {
    text-align: center;
}

.c-title__ja {
    font-size: rem(20);
    @include mq(md) {
        font-size: rem(32);
    }
}

.c-title__img {
    width: rem(33);
    margin: 0 auto;
    margin-top: rem(5);
}

.c-title__en {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: bold;
    color: $green;
    margin-top: rem(9);
    @include mq(md) {
        font-size: rem(24);
    }
}

.c-title__en--white {
    color: $white;
}