@use 'global' as *;
@use 'sass:math';

.p-hamburger {
    position: relative;
    width: rem(60);
    height: rem(60);
    background-color: $black;
    border-radius: rem(20);

    // オープン
    &.is-active {
        
        span:first-child {
            top: rem(29);
            left: 25%;
            transform: rotate(45deg);
        }
        span:nth-child(2) {
            display: none;
        }
        span:last-child {
            top: rem(29);
            left: 25%;
            transform: rotate(-45deg);
        }
    }
}


.p-hamburger span {
    position: absolute;
    display: inline-block;
    background-color: $white;
    width: rem(30);
    height: rem(2);
    left: 50%;
    transform: translateX(-50%);
    border-radius: rem(2);
    transition: all 0.3s;
}

.p-hamburger span:first-child {
    top: rem(19);
}

.p-hamburger span:nth-child(2) {
    top: rem(29);
}

.p-hamburger span:last-child {
    top: rem(39);
}