@use "global" as *;
@use 'sass:math';

.c-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: rem(20);
    font-weight: 500;
    text-align: center;
    background-color: $black;
    color: $white;
    width: 100%;
    line-height: math.div(rem(26),rem(20));
    padding: rem(23) 0;
    border-radius: rem(100);
    transition: all 0.3s;
    @include mq(md) {
        padding: rem(16) 0;
        font-size: rem(16);
        line-height: math.div(rem(26),rem(16));
    }
}

.c-btn:hover {
    @include mq(md) {
        background-color: $yellow;
        color: $black;
    }
}

.c-btn img {
    display: inline-block;
    width: rem(20);
    margin-left: rem(20);
    @include mq(md) {
        width: rem(21);
    }
}
