@use 'global' as *;
@use 'sass:math';

.p-footer {
    background-color: $white;
    padding-top: rem(32);
    @include mq(md) {
        background: url(../images/common/bg-footer.png) center center/ cover no-repeat;
        padding-top: rem(254);
    }
}

.p-footer__inner {

}

.p-footer__img {
    max-width: rem(186);
    width: 100%;
    margin: 0 auto;
    @include mq(md) {
        display: none;
    }
}

.p-footer__nav {

}

.p-footer__links {
    margin-top: rem(48);
    @include mq(md) {
        display: flex;
        justify-content: center;
    }
}

.p-footer__link {
    
}

.p-footer__link + .p-footer__link {
    margin-top: rem(32);
    @include mq(md) {
        margin-top: rem(0);
        margin-left: rem(48);
    }
}

.p-footer__link a {
    display: block;
    text-align: center;
}

.p-footer__link img {
    max-width: rem(19);
    margin: 0 auto;
}

.p-footer__link span {
    font-size: rem(16);
    @include mq(md) {
        margin-top: rem(5);
    }
}

.p-footer__copy {
    background-color: #C9DDAC;
    width: 100vw;
    height: rem(40);
    font-size: rem(10);
    text-align: center;
    line-height: rem(40);
    margin-left: calc(50% - 50vw);
    margin-top: rem(48);
    @include mq(md) {
        background-color: transparent;
        margin-top: rem(60);
    }
}