@use 'global' as *;
@use 'sass:math';

.p-content {
    @include mq(md) {
        background-color: $green;
        padding-top: rem(32);
        padding-bottom: rem(52);
        border-radius: rem(36) 0 0 0;
    }
}

.p-content__date {
    max-width: rem(200);
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0 auto;
}

.p-content__today {
    font-size: rem(16);
}

.p-content__month {
    margin-left: rem(9);
}

.p-content__month,
.p-content__day {
    background: url(../images/common/date.svg) center center / contain no-repeat;
    width: rem(60);
    height: rem(60);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.p-content__month span,
.p-content__day span {
    display: block;
    text-align: center;
}

.p-content__month span:first-child,
.p-content__day span:first-child {
    font-size: rem(14);
    font-weight: 400;
}

.p-content__month span:last-child,
.p-content__day span:last-child {
    font-size: rem(24);
    margin-top: rem(3);
}

.p-content__month span {

}

.p-content__day {
    margin-left: rem(13);

}

.p-content__day span {

}

.p-content__tag  {
    position: relative;
    display: block;
    width: rem(200);
    text-align: center;
    border-radius: rem(10);
    z-index: 1;
    margin: 0 auto;
    margin-top: rem(20);
    box-shadow: rem(2) rem(2) 0 $white;
    @include mq(md) {
        max-width: rem(200);
        width: 100%;
        padding: rem(10) 0;
    }
}

.p-content__box {
    width: 100%;
    border: 2px solid #171717;
    border-radius: rem(10);
    box-shadow: rem(5) rem(5) 0 $white;
    margin-top: rem(33);
    @include mq(md) {
        max-width: rem(200);
        width: 100%;
        margin: 0 auto;
        margin-top: rem(32);
    }
}

.p-content__box-head {
    font-size: rem(16);
    font-weight: 400;
    text-align: center;
    background-color: $yellow;
    border-bottom: 2px solid #171717;
    border-radius: rem(10) rem(10) 0 0;
    padding: rem(12);
}

.p-content__box-main {
    background-color: $white;
    border-radius: 0 0 rem(10) rem(10);
    padding-bottom: rem(17);
}

.p-content__box-text {
    max-width: rem(290);
    width: 100%;
    margin: 0 auto;
    font-size: rem(16);
    font-weight: 400;
    text-align: center;
    border-bottom: 1px dashed #000;
    padding: rem(15) 0;
    @include mq(md) {
        max-width: rem(168);
        width: 100%;
        padding: rem(10) 0;
    }
}

.p-content__pickup {
    position: relative;
    z-index: 2;
    margin-top: rem(38);
}

.p-content__star {
    width: rem(40);
    margin: 0 auto;
}

.p-content__title {
    max-width: rem(170);
    width: 100%;
    margin: 0 auto;
    margin-top: rem(-35);
    @include mq(md) {
        max-width: rem(201);
    }
}

.p-content__img {
    position: relative;
    max-width: rem(203);
    width: 100%;
    margin: 0 auto;
    margin-top: rem(-80);
}

.p-content__img::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: $white;
    bottom: rem(-2);
    right: rem(-2);
    border-radius: 50%;
    z-index: -1;
}

.p-content__img::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: $black;
    bottom: rem(-5);
    right: rem(-6);
    border-radius: 50%;
    z-index: -2;
}

.p-content__sns {
    position: relative;
    background-color: $white;
    max-width: rem(203);
    border: 2px solid #171717;
    border-radius: rem(10);
    width: 100%;
    height: rem(57);
    box-shadow: rem(5) rem(5) 0 $white;
    margin: 0 auto;
    margin-top: rem(40);
}

.p-content__sns-title {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: rem(-12);
    background-color: $yellow;
    font-size: rem(16);
    font-weight: bold;
    font-family: $second-font-family;
    text-align: center;
    width: rem(80);
    padding: rem(3) 0 0;
    border: 2px solid #171717;
    border-radius: rem(5);
    box-shadow: rem(2) rem(0) 0 $white;
}

.p-content__sns-links {
    display: flex;
    padding-top: rem(10);
    align-items: center;
    justify-content: center;
    height: 100%;
}

.p-content__sns-link {
    height: 100%;
}

.p-content__sns-link + .p-content__sns-link {
    margin-left: rem(24);
}

.p-content__sns-link a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.p-content__sns-link img {
    width: rem(36);
}

.p-content__top {
    display: none;
    @include mq(md) {
        display: block;
        background-color: $white;
        border: 2px solid #171717;
        border-radius: rem(10);
        box-shadow: rem(2) rem(2) 0 $white;
        padding: rem(35) rem(15);
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: rem(201);
        width: 100%;
        margin: 0 auto;
    }
}

.p-content__top .c-title__ja {
    font-weight: 400;
}

.p-content__top .c-title__en {
    font-size: rem(20);
    margin-top: rem(28);
}

.p-content__date--animals {
    @include mq(md) {
        margin-top: rem(54);
    }
}