@use 'global' as *;

.p-mv {
    background-color: $green;
    padding-top: rem(33);
    padding-bottom: rem(32);
    @include mq(md) {
        background: linear-gradient(90deg, transparent 0%,transparent 20%,$green 20%, $green 100%);
        padding-top: 0;
        max-width: rem(1440);
        margin: 0 auto;
    }
}

.p-mv__inner.l-inner {
    padding: 0;
    padding-top: rem(35);
    @include mq(md) {
        max-width: rem(1362);
        padding-left: 25px;
        padding-right: 25px;
        padding-top: 0;
    }
}

.p-mv__wrap {
    display: flex;
    flex-direction: column-reverse;
    @include mq(md) {
        flex-direction: row;
        align-items: flex-start;
    }
}

.p-mv__content {
    margin-top: rem(32);
    @include mq(md) {
        width: rem(300);
        margin-top: 0;
        margin-bottom: rem(-32);
    }
}

.p-mv__items {
    width: 100vw;
    border: 1px solid #00262B;
    border-radius: rem(10) rem(10) 0 0;
    margin-left: calc(50% - 50vw);
    @include mq(md) {
        border: none;
        border-radius: 0 rem(10) 0 0;
        width: calc(100% - #{rem(300)});
        margin-left: 0;
    }
}

.p-mv__item {
    position: relative;
    width: 100%;
    border-radius: rem(10);
    overflow: hidden;

    @include mq(md) {
        margin-left: 0;
        border: none;
        border-radius: 0 rem(10) 0 0;
    }
}

.p-mv__item::before {
    display: block;
    content: "";
    padding-top: 96.15%;
    @include mq(md) {
        padding-top: 75.39%;
    }
}

.p-mv__item img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

.p-mv__box {
    border: 1px solid #171717;
    border-radius: rem(20);
    background-color: $white;
    margin-top: rem(32);
    @include mq(md) {
        max-width: rem(800);
        margin: 0 auto;
        margin-top: rem(60);
        border-width: rem(2);
    }
}

.p-mv__box--small {
    max-width: calc(100% - #{rem(30)});
    margin: 0 auto;
    margin-top: rem(32);
    @include mq(md) {
        max-width: rem(800);
        margin: 0 auto;
        margin-top: rem(60);
        border-width: rem(2);
    }
}

.p-mv__box-head {
    text-align: center;
    padding: rem(10) 0;
    border-bottom: 1px solid #171717;
}

.p-mv__box-tags {
    display: flex;
    flex-wrap: wrap;
    padding: rem(13) rem(23) rem(36);
    margin-left: rem(-23);
    @include mq(md) {
        padding: rem(0) rem(40) rem(16);
    }
}

.p-mv__box-tag {
    margin-top: rem(20);
    margin-left: rem(28);
}

.p-mv__box-tag + .p-mv__box-tag {

}

.p-mv__top {
    border: 1px solid #171717;
    border-radius: rem(10) rem(10) 0 0;
    background-color: $white;
    @include mq(md) {
        border-width: rem(2);
        border: none;
        border-radius: rem(10);
        overflow: hidden;
    }
}

.p-mv__top-head {
    text-align: center;
    padding: rem(10) 0;
    border-bottom: 1px dashed #171717;
    @include mq(md) {
        display: none;
    }
}

.p-mv__slider {
    padding: rem(27) rem(18) rem(40);
    @include mq(md) {
        padding: 0;
    }
}

.p-mv__slider--info {
    max-width: rem(316);
    margin: 0 auto;
    @include mq(md) {
        max-width: rem(616);
    }
}

.p-mv__slider--info .slick-list {
    overflow: unset;
}

.p-mv__slider-item {
    width: 100%;
    position: relative;
}

.p-mv__slider-item {

}

.p-mv__slider-item::before {
    display: block;
    content: "";
    padding-top: 63.7%;
    @include mq(md) {
        padding-top: 50%;
    }
}

.p-mv__slider-item--info {
    margin: 0 rem(3.5);
    @include mq(md) {
       margin: 0 rem(19);
    }
}

.p-mv__slider--info {
    padding-bottom: rem(56);
    @include mq(md) {
        padding: rem(72) 0;
    }
}

.p-mv__slider-item--info::before {
    padding-top: 100%;
    @include mq(md) {
        padding-top: 100%;
    }
}

.p-mv__slider-item img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-mv__slider-item--info img {
    border-radius: 50%;
}

.p-mv__top-text {
    text-align: left;
    max-width: rem(280);
    margin: 0 auto;
    margin-top: rem(8);
    @include mq(md) {
        display: none;
    }
}

.p-mv__main {
    max-width: rem(912);
    width: 100%;
    @include mq(md) {
        margin-top: rem(61);
        width: calc(100% - 18.75rem);
    }
}

.p-mv__main--info {
    @include mq(md) {
        margin-top: 0;
    }
}

.p-mv__main--info {
    .prev-arrow {
        left: rem(-10);
        @include mq(md) {
            left: rem(-90);
        }
    }
    .next-arrow {
        right: rem(-10);
        @include mq(md) {
            right: rem(-90);
        }
    }
}