@use 'global' as *;

.p-nav {
    position: relative;
}

.p-nav__btn {
    max-width: rem(345);
    margin: 0 auto;
    @include mq(md) {
        max-width: rem(260);
    }
}

.p-nav__arrows {
    display: flex;
    justify-content: space-between;
    padding: 0 rem(20);
    margin-top: rem(42);
    @include mq(md) {
        max-width: rem(816);
        width: 100%;
        position: absolute;
        left: 50%;
        transform: translate(-50%,-50%);
        top: 50%;
        margin-top: 0;
    }
}

.p-nav__arrow {
    max-width: rem(133);
    width: 100%;
}

.p-nav__arrow a {
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
}

.p-nav__arrow img {
    width: rem(25);
    height: rem(25);
}

.p-nav__pager {
    display: flex;
    justify-content: center;
    align-items: center;
}

.p-nav__page {
    width: rem(50);
    height: rem(50);
    border-radius: 50%;
    background-color: $white;
    border: 2px solid $white;
}

.p-nav__page + .p-nav__page {
    margin-left: rem(24);
}

.p-nav__page.is-active {
    border-color: #171717;
}

.p-nav__page a {
    display: block;
    width: 100%;
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: rem(16);
}