@use 'global' as *;
@use 'sass:math';

.p-article {
    background-color: $green;
    padding-bottom: rem(42);
    @include mq(md) {
        max-width: rem(1440);
        margin: 0 auto;
        padding-bottom: rem(80);
    }
}

.p-article__inner.l-inner {
    padding: 0;
    @include mq(md) {
        padding: 0 rem(15);
    }
}

.p-article__box {
    background-color: $white;
    border-radius: rem(10);
    padding: rem(48) rem(15) rem(22);
    @include mq(md) {
        border: 2px solid #171717;
        padding: rem(40) rem(15);
        border-radius: 1.25rem;
    }
}

.p-article__box-inner {
    @include mq(md) {
        max-width: rem(812);
        margin: 0 auto;
    }
}

.p-article__text {
    font-size: rem(16);
}

.p-article__text + .p-article__text {
    margin-top: rem(10);
}

.p-article__title {
    font-size: rem(28);
    margin-top: rem(30);
    line-height: 1.5;
    @include mq(md) {
        margin-top: rem(20);
    }
}

.p-article__box img {
    display: block;
    width: auto;
    position: relative;
}

.p-article__box p {
    margin-top: rem(28);
    line-height: 2;
    @include mq(md) {
        margin-top: rem(18);
    }
}

.p-article__subTitle {
    font-size: rem(28);
    margin-top: rem(85);
    @include mq(md) {
        margin-top: rem(20);
    }
}

.p-article__items {
    margin-top: rem(25);
    @include mq(md) {
        display: grid;
        gap: rem(21);
        grid-template-columns: repeat(3,1fr);
    }
}

.p-article__item {
    width: 100%;
    position: relative;
    border-radius: rem(10);
    overflow: hidden;
}

.p-article__item::before {
    display: block;
    content: "";
    padding-top: 71%;
    @include mq(md) {
        padding-top: 82%;
    }
}

.p-article__item img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-article__item + .p-article__item {
    margin-top: rem(28);
    @include mq(md) {
        margin-top: 0;
    }
}

.p-article__nav {
    margin-top: rem(42);
    @include mq(md) {
        margin-top: rem(80);
    }
}

.p-article__btn {
    
}


.p-article__date {
    display: flex;
}

.p-article__date span {
    display: inline-block;
}

.p-article__date span:first-child {
    color: rgba($color: #171717, $alpha: 0.6);
}

.p-article__date span + span {
    margin-left: rem(12);
}

.p-article__tag {
    margin-top: rem(8);
}

.p-article__wrap {
    margin-top: rem(80);
}

.p-article__wrap img {
    float: left;
    width: 50%;
    max-width: rem(400);
    margin-right: rem(28);
}