@use 'global' as *;
@use 'sass:math';

.p-about {
    position: relative;
    background-color: $white;
    border-radius: 0 rem(80) 0 rem(80);
    padding-top: rem(32);
    padding-bottom: rem(60);
    box-shadow: 0 rem(4) rem(4) rgba($color: #000000, $alpha: .25);
}

.p-about__inner {

}

.p-about__title {

}

.p-about__text {
    margin-top: rem(32);
    @include mq(md) {
        max-width: rem(800);
        width: 100%;
        margin: 0 auto;
        margin-top: rem(32);
    }
}

.p-about__btn {
    max-width: rem(320);
    margin: 0 auto;
    margin-top: rem(60);
    @include mq(md) {
        max-width: rem(280);
        margin-top: rem(20);
    }
}

.p-about__map {
    margin-top: rem(60);
    @include mq(md) {
        margin-top: rem(-30);
    }
}

.p-about__map-tag {
    max-width: rem(200);
    margin: 0 auto;
    position: relative;
    z-index: 1;
    @include mq(md) {
        width: rem(181);
        margin-left: 0;
    }
}

.p-about__map-img {
    width: 100%;
    margin-top: rem(-12);
    @include mq(md) {
        max-width: rem(1046);
        margin: 0 auto;
        margin-top: rem(-107);
    }
}
