@use 'global' as *;
@use 'sass:math';

.p-header {
    width: 100%;
    height: rem(96);
    border-bottom: 1px solid #171717;
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #F1EFDE;
    
    
    @include mq(md) {
        position: initial;
        max-width: rem(1440);
        height: rem(139);
        margin: 0 auto;
        border: none;
        background: linear-gradient(90deg, transparent 0%,transparent 50%,$green 50%, $green 100%);
    }
}

.p-header__inner {
    display: flex;
    height: inherit;
    @include mq(md) {
        height: 100%;
        max-width: rem(1362);
        padding-left: 25px;
    }

    @include mq(md) {
        max-width: rem(1362);
        padding-right: 0;
    }
}

.p-header__logo {
    display: grid;
    place-items: center;
    @include mq(md) {
        width: calc(#{rem(300)});
    }
}

.p-header__logo a {
    max-width: rem(150);
}


.p-header__globalNav {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    @include mq(md) {
        background-color: $green;
        border-radius:rem(36)  0 0 0;
        padding-right: 25px;
        margin-left: 0;
        width: calc(100% - #{rem(325)});
    }

    @include mq(lg) {
        width: calc(100% - 18.5rem);
    }

    @include mq(xl) {
        width: calc(100% - #{rem(300)} - 25px );
    }
}

.p-header__hamburger {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: rem(20);
    z-index: 1002;
    @include mq(md) {
        display: none;
    }
}

.p-header__drawer {
    z-index: 999;
}