@use 'global' as *;
@use 'sass:math';

.p-drawer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $green;
    padding-top: rem(116);
    overflow: scroll;
    transform: translateX(105%);
    transition: all 0.5s ease-in;

    &.is-active {
        transform: translateX(0);
    }
    @include mq(md) {
        display: none;
    }
}

.p-drawer__inner {
    margin-bottom: rem(30);
}

.p-drawer__nav {
    border: 2px solid #171717;
    border-radius: rem(10);
    background-color: $white;
    padding: rem(32) rem(30) rem(40);

}

.p-drawer__menus {

}

.p-drawer__menu {
    position: relative;
}

.p-drawer__menu.is-open::before {
    transform: rotate(180deg);
}

.p-drawer__menu:nth-child(3)::before {
    position: absolute;
    content: "";
    background: url(../images/common/arrowBottom.svg) center center / contain no-repeat;
    width: rem(20);
    height: rem(8);
    top: rem(10);
    right: rem(0);
    transition: all 0.3s;
}

.p-drawer__menu + .p-drawer__menu {
    margin-top: rem(50);
}

.p-drawer__menu a {
    display: inline-flex;
    align-items: center;
    margin-left: 0;
    width: 100%;
}

.p-drawer__menu img {
    width: rem(19);
}

.p-drawer__menu span {
    font-size: rem(20);
    font-weight: 400;
    margin-left: rem(8);
}

.p-drawer__subMenus {
    background-color: #F1EFDE;
    padding: rem(12) rem(28);
    margin-top: rem(10);
    border-radius: rem(10);
}

.p-drawer__subMenu {

}

.p-drawer__subMenu + .p-drawer__subMenu {
    margin-top: rem(20);
}

.p-drawer__subMenu a {
    display: block;
}

.p-drawer__subMenu span {
    font-size: rem(16);
    font-weight: 400;
}