@use 'global' as *;
@use 'sass:math';

.p-access {
    background-color: $green;
    border-radius: 0 rem(80) 0 rem(80);
    padding-top: rem(32);
    padding-bottom: rem(60);
    box-shadow: 0 rem(4) rem(4) rgba($color: #000000, $alpha: .25);
    @include mq(md) {
        position: relative;
        padding-bottom: rem(38);
    }
}

.p-access__inner {

}

.p-access__title {
    text-align: center;
}

.p-access__box {
    background-color: $white;
    padding: rem(35) rem(8) rem(12);
    border-radius: 0 0 rem(10) rem(10);
    @include mq(md) {
        position: relative;
        max-width: rem(800);
        margin: 0 auto;
    }
}

.p-access__map {
    position: relative;
    width: 100%;
    margin-top: rem(18);
    @include mq(md) {
        max-width: rem(800);
        margin: 0 auto;
        margin-top: rem(34);
    }
}

.p-access__map::before {
    padding-top: 77.39%;
    content: "";
    display: block;
    @include mq(md) {
        padding-top: 50%;
    }
}

.p-access__map img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-access__info {
    @include mq(md) {
        display: flex;
        flex-wrap: wrap;
        padding: rem(31) rem(28) rem(28);
        margin-left: rem(-85);
    }
}

.p-access__unit {
    @include mq(md) {
        margin-left: rem(85);
    }
}

.p-access__unit + .p-access__unit {
    margin-top: rem(40);
    @include mq(md) {
        margin-top: rem(0);
    }
}

.p-access__unit:last-child {
    @include mq(md) {
        margin-top: rem(29);
    }

}

.p-access__unit-title {
    font-size: rem(20);
    line-height: math.div(rem(26),rem(20));
}

.p-access__unit-title span {
    display: inline-block;
    color: $yellow;
    font-size: rem(20);
    margin-right: rem(8);
}

.p-access__unit-text {
    line-height: math.div(rem(26),rem(16));
    margin-top: rem(8);
}

.p-access__unit-img {
    margin-top: rem(40);
}

.p-access__img {
    max-width: rem(310);
    margin: 0 auto;
    margin-top: rem(36);
    @include mq(md) {
        position: absolute;
        bottom: rem(-10);
        right: rem(38);
    }
}

.p-access__tree01 {
    position: absolute;
    width: rem(219);
    right: calc(50% - #{rem(607)});
    top: rem(32);
}

.p-access__tree02 {
    position: absolute;
    width: rem(198);
    bottom: rem(39);
    left: calc(50% - #{rem(588)});
}