@use 'global' as *;

.p-card {
    border: 2px solid #00262B;
    border-radius: rem(10);
    background-color: $white;
    padding: rem(16) rem(12);
    box-shadow: rem(5) rem(5) 0 $white;
    height: 100%;
}

.p-card + .p-card {
   
}

.p-card__img {
    max-width: rem(282);
    width: 100%;
    position: relative;
    border-radius: rem(10);
    overflow: hidden;
    @include mq(md) {
        margin: 0 auto;
    }
}

.p-card__img::before {
    display: block;
    content: "";
    padding-top: 59.219%;
}

.p-card__img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-card__date {
    display: flex;
    align-items: center;
    justify-content: left;
    margin-top: rem(20);
}

.p-card__date-post {
    font-size: rem(12);

}

.p-card__date-update {
    font-size: rem(12);
    margin-left: rem(12);
}

.p-card__tag  {
    margin-top: rem(8);
}

.p-card__title {
    margin-top: rem(15);
}

.p-card__text {
    margin-top: rem(12);
}