@use 'global' as *;
@use 'sass:math';

.p-info {
    position: relative;
}

.p-info__inner {

}

.p-info__title {

}

.p-info__units {
    margin-top: rem(78);
}

.p-info__unit {
    position: relative;
    border: 2px solid #00262B;
    border-radius: rem(10);
    background-color: $white;
    padding: rem(16) rem(21);
    box-shadow: rem(5) rem(5) 0 $white;
    @include mq(md) {
        margin-left: rem(40);
    }
}

.p-info__unit a {
    display: block;
    width: 100%;
    height: 100%;
    @include mq(md) {
        display: flex;
        align-items: center;
        padding: rem(24) rem(40);
    }
}

.p-info__unit + .p-info__unit {
    margin-top: rem(71);
    @include mq(md) {
        margin-top: rem(48);
    }
}

.p-info__unit-number {
    position: absolute;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 2px solid #00262B;
    width: rem(60);
    height: rem(60);
    background-color: $yellow;
    left: 50%;
    transform: translateX(-50%);
    top: rem(-53);
    @include mq(md) {
        width: rem(80);
        height: rem(80);
        left: rem(-40);
        top: rem(-40);
        transform: none;
    }
}

.p-info__unit-number span {
    font-size: rem(28);
}

.p-info__unit-tag {

}

.p-info__unit-text {
    margin-top: rem(20);
    @include mq(md) {
        margin-top: rem(0);
        margin-left: rem(60);
    }
}

.p-info__tree01 {
    position: absolute;
    width: rem(183);
    height: auto;
    top: rem(-30);
    left: calc(50% - #{rem(698)});
}

.p-info__tree02 {
    position: absolute;
    width: rem(183);
    height: auto;
    bottom: rem(-75);
    right: calc(50% - #{rem(698)});
}